/* ---- isotope ---- */
.isotope-filters {
  display: none;
}
@media (min-width: 768px) {
  .isotope-filters {
    display: flex;
    margin-top: 2em;
    margin-bottom: 2em;
  }
}
.isotope-filters .isotope-filters-popup-close {
  display: none;
}
.isotope-filters.isotope-filters-popup {
  display: flex;
  position: fixed;
  z-index: 100;
  left: 20px;
  right: 20px;
  top: 0;
  bottom: 0;
  padding: 20px;
  background: rgba(255, 255, 255, 0.95);
  align-content: center;
}
@media (min-width: 768px) {
  .isotope-filters.isotope-filters-popup {
    display: flex;
    position: static;
    padding: 0;
  }
}
.isotope-filters.isotope-filters-popup .isotope-filters-popup-close {
  display: block;
  position: absolute;
  top: 20px;
  right: 20px;
}
@media (min-width: 768px) {
  .isotope-filters.isotope-filters-popup .isotope-filters-popup-close {
    display: none;
  }
}
@media (min-width: 768px) {
  .isotope-filters-trigger {
    display: none;
  }
}
.isotope-grid {
  margin: 2em auto;
  padding: 20px 0 0 20px;
  max-width: none;
}
@media (min-width: 768px) {
  .isotope-grid {
    max-width: 760px;
  }
}
@media (min-width: 960px) {
  .isotope-grid {
    max-width: 950px;
  }
}
@media (min-width: 1440px) {
  .isotope-grid {
    max-width: 1420px;
  }
}
/* clear fix */
.isotope-grid:after {
  content: '';
  display: block;
  clear: both;
}
/* grid-item */
.isotope-grid-sizer {
  float: left;
  width: calc(100% - 40px);
  margin: 0 20px 0 0;
}
@media (min-width: 768px) {
  .isotope-grid-sizer {
    width: 350px;
  }
}
@media (min-width: 960px) {
